.lifestyle {
	height: 100%;
	width: 100%;
	position: relative;
	padding: 100px 0;
	background-image: url(../../../public/assets/images/lifestyle_bottom-right.svg),
		url(../../../public/assets/images/lifestyle_top-left.svg);
	background-repeat: no-repeat;
	background-position: bottom right, top left;
}

.lifestyle__title {
	text-align: center;
	font-size: 3.5em;
}

.lifestyle__title span {
	color: var(--primary-theme-clr);
}

.lifestyle .lifestyle__product-wrapper {
	display: flex;
	align-items: center;
	flex-direction: column;
	margin-top: 50px;
}

.lifestyle .lifestyle__product-wrapper > div {
	width: 70%;
	display: flex;
	justify-content: space-around;
	align-items: center;
	text-align: center;
	margin-top: 100px;
}

.lifestyle .lifestyle__product-wrapper > div:last-child {
	flex-direction: row-reverse;
	margin-top: 200px;
}

.lifestyle .lifestyle__product-wrapper > div img {
	width: 40%;
}

.lifestyle .lifestyle__product-wrapper > div .product-link {
	width: 100%;
	text-decoration: none;
}

.lifestyle .lifestyle__product-wrapper > div .product-card {
	width: 100%;
	height: 100px;
	background-color: var(--primary-theme-clr);
	display: grid;
	place-items: center;
	font-size: 2rem;
	font-weight: 600;
	text-transform: uppercase;
	border-radius: 10px;
	color: var(--secondary-theme-clr);
}

.lifestyle .lifestyle__product-wrapper > div .product-content {
	padding: 0 8%;
}

.lifestyle .lifestyle__product-wrapper > div .product-content h3 {
	font-size: 50px;
}

.lifestyle .lifestyle__product-wrapper > div .product-content h6 {
	color: #000;
	font-size: 28px;
}

.lifestyle .lifestyle__product-wrapper > div .product-content p {
	margin-top: 20px;
	font-size: 20px;
	text-align: justify;
}

@media (max-width: 480px) {
	.lifestyle {
		padding: 35px 30px;
		background-size: 80%;
	}

	.lifestyle .lifestyle__title {
		font-size: 30px;
	}

	.lifestyle .lifestyle__product-wrapper {
		margin-top: 0;
	}

	.lifestyle .lifestyle__product-wrapper > div {
		width: 90%;
		flex-direction: column-reverse;
		margin-top: 60px;
	}

	.lifestyle .lifestyle__product-wrapper > div .product-content h6 {
		font-size: 24px;
	}

	.lifestyle .lifestyle__product-wrapper > div .product-content p {
		font-size: 17px;
	}

	.lifestyle .lifestyle__product-wrapper > div .product-card {
		margin-top: 20px;
		height: 50px;
		width: 90%;
		margin: auto;
		font-size: 1rem;
		margin-top: 20px;
	}

	.lifestyle .lifestyle__product-wrapper > div:last-child {
		margin-top: 90px;
		flex-direction: column-reverse;
	}
}
