.whyus {
	height: 100%;
	width: 100%;
	padding-top: 60px;
	background-image: url(../../../public/assets/images/whyus_top-right.svg);
	background-repeat: no-repeat;
	background-position: top right;
}

.whyus .whyus__title {
	text-align: center;
	font-size: 3.5em;
}

.whyus .whyus__title span {
	color: var(--primary-theme-clr);
}

.whyus .whyus__subtitle {
	text-align: center;
	font-size: 2.5em;
	margin-top: 80px;
	padding: 0 10%;
}

.whyus .whyus__features-wrapper {
	display: flex;
	align-items: flex-start;
	flex-direction: column;
	width: 80%;
	margin: 100px auto;
}

.whyus .whyus__features-wrapper .feature {
	margin: 30px;
	width: 80%;
	text-align: center;
	width: 50%;
}

.whyus .whyus__features-wrapper .feature:nth-child(2) {
	margin-left: 50%;
	width: fit-content;
}

.whyus .whyus__features-wrapper .feature h4 {
	color: var(--primary-theme-clr);
	font-size: 27px;
	margin-bottom: 20px;
}

.whyus .whyus__features-wrapper .feature p {
	color: #000;
	font-size: 26px;
	font-weight: 400;
}

@media (max-width: 480px) {
	.whyus {
		padding-top: 110px;
		background-size: 79%;
	}

	.whyus .whyus__subtitle {
		font-size: 1.5em;
		margin-top: 50px;
	}

	.whyus .whyus__features-wrapper {
		margin: 50px auto;
		width: 100%;
	}

	.whyus .whyus__features-wrapper .feature {
		width: fit-content;
	}

	.whyus .whyus__features-wrapper .feature:nth-child(2) {
		margin-left: 30px;
		text-align: center;
	}

	.whyus .whyus__features-wrapper .feature h4 {
		font-size: 30px;
	}

	.whyus .whyus__features-wrapper .feature p {
		font-size: 22px;
	}
}
