.founder-story {
	height: 80vh;
	width: 100%;
	display: grid;
	place-items: center;
	padding: 0 8%;
	background-image: url(../../../public/assets/images/founder-story_bottom-left.svg), url(../../../public/assets/images/founder-story_top-right.svg);
	background-repeat: no-repeat;
	background-position: bottom left, top right;
}

.founder-story > p {
	font-size: 64px;
	text-align: center;
	color: #6f6c99;
}

.founder-story > p > a {
	display: inline-block;
	border: 0;
	padding: 15px 40px;
	font-size: 32px;
	cursor: pointer;
	background-color: var(--primary-theme-clr);
	color: var(--secondary-theme-clr);
	border: 2px solid var(--secondary-theme-clr);
	margin-top: 100px;
	border-radius: 10px;
	font-weight: bold;
	text-decoration: none;
}

.founder-story > p > a:hover {
	background-color: var(--secondary-theme-clr);
	color: var(--primary-theme-clr);
	border: 2px solid var(--primary-theme-clr);
}

@media (max-width: 480px) {
	.founder-story {
		height: 100%;
		margin: 20px 0 0;
		background-size: 60%;
	}

	.founder-story > p {
		font-size: 32px;
	}

	.founder-story > p > a {
		margin-top: 40px;
		font-size: 18px;
	}
}
