.doctors {
	height: 100%;
	width: 100%;
}

.doctors .doctors__title {
	font-size: 3em;
	text-align: center;
}

.doctors .doctors__title span {
	color: var(--primary-theme-clr);
}

.doctors .doctors__doctors-wrapper {
	display: flex;
	justify-content: space-around;
	margin: 100px auto 0;
	width: 75%;
	text-align: center;
}

.doctors .doctors__doctors-wrapper .doctor__profile {
	margin-top: 50px;
}

.doctors .doctors__doctors-wrapper .doctor__profile > div {
	width: 250px;
	height: 250px;
	margin: auto;
	background-position: center;
	background-size: cover;
	border-radius: 50%;
}

.doctors .doctors__doctors-wrapper .doctor__profile > p {
	margin-top: 20px;
	font-size: 20px;
	font-weight: 500;
}

.doctors .doctors__doctors-wrapper .doctor__profile > p span {
	display: block;
	color: var(--primary-theme-clr);
	font-weight: bold;
	font-size: 26px;
}

@media (max-width: 480px) {
	.doctors {
		padding: 80px 20px;
	}

	.doctors .doctors__title {
		font-size: 30px;
	}

	.doctors .doctors__doctors-wrapper {
		flex-direction: column-reverse;
		margin: 0 auto;
		width: 100%;
	}

	.doctors .doctors__doctors-wrapper .doctor__profile {
		margin-top: 80px;
	}
}
