header {
	height: 100vh;
	width: 100%;
	background-position: bottom right;
	background-size: contain;
	background-repeat: no-repeat;
	display: flex;
	justify-content: center;
	flex-direction: column;
	padding: 0 5%;
}

header .header___content {
	margin: 30px 0;
}

header h1.header__title {
	color: #000;
	font-size: 4rem;
	font-weight: 800;
}

header h1.header__title span {
	color: var(--primary-theme-clr);
}

header .header__product-wrapper {
	margin: 70px 0;
}

header .header__product-wrapper a {
	border: 0;
	border-radius: 5px;
	padding: 20px 40px;
	margin-right: 60px;
	cursor: pointer;
	background: var(--primary-theme-clr);
	color: var(--secondary-theme-clr);
	border: 2px solid var(--secondary-theme-clr);
	font-size: 24px;
	font-weight: 600;
	transition: 0.2s all ease-in-out;
	text-decoration: none;
}

header .header__product-wrapper a:hover {
	transform: scale(1.05);
	background: var(--secondary-theme-clr);
	color: var(--primary-theme-clr);
	border: 2px solid var(--primary-theme-clr);
}

@media (max-width: 480px) {
	header {
		background-size: cover;
		justify-content: flex-start;
		padding-top: 40%;
	}

	header h1.header__title {
		text-align: center;
		font-size: 30px;
	}

	header .header__product-wrapper {
		margin: 40px 0;
	}

	header .header__product-wrapper a {
		display: block;
		width: 100%;
		margin: 30px 0;
		font-size: 17px;
		text-align: center;
	}
}
