nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 100px;
	width: 100%;
	padding: 20px 2% 20px 5%;
	position: fixed;
	background-color: var(--secondary-theme-clr);
	z-index: 10;
	transition: 0.4s all ease-in-out;
}

nav .nav__logo a {
	text-transform: uppercase;
	font-size: 32px;
	color: #000;
	font-weight: 700;
	text-decoration: none;
}

nav .mobile__menu {
	display: none;
}

nav .mobile__menu .hamburger {
	height: 2px;
	width: 25px;
	background-color: var(--hamburger-menu-clr);
	position: relative;
	display: none;
	transition: 0.5s all ease-in-out;
}

nav .mobile__menu .hamburger::before,
nav .mobile__menu .hamburger::after {
	content: "";
	height: 2px;
	width: 25px;
	background-color: var(--hamburger-menu-clr);
	position: absolute;
	transition: 0.5s all ease-in-out;
}

nav .mobile__menu .hamburger::before {
	transform: translateY(-6px);
}

nav .mobile__menu .hamburger::after {
	transform: translateY(6px);
}

nav .mobile__menu .menu-slider {
	position: absolute;
	height: 0;
	width: 100%;
	background-color: var(--secondary-theme-clr);
	top: 100px;
	right: 0;
	opacity: 0.95;
	overflow-y: hidden;
	transition: 0.7s height;
}

nav .mobile__menu .menu-slider ul {
	width: 100%;
	height: 75%;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-direction: column;
}

nav .mobile__menu .menu-slider ul li {
	list-style: none;
	margin: 30px;
}

nav .mobile__menu .menu-slider ul li a {
	font-size: 30px;
	text-decoration: none;
	color: #000;
	font-weight: 400;
}

nav .mobile__menu #hamburger {
	height: 35px;
	width: 35px;
	position: absolute;
	top: 30px;
	right: 40px;
	opacity: 0;
	z-index: 1;
}

nav .nav__menu ul {
	display: flex;
	justify-content: center;
	align-items: center;
}

nav .nav__menu ul li {
	list-style: none;
	margin: 0 20px;
}

nav .nav__menu ul li .menu-item {
	color: #000;
	text-decoration: none;
	position: relative;
	font-weight: 500;
}

nav .nav__menu ul li:hover .menu-item::after {
	content: "";
	height: 5px;
	width: 50%;
	position: absolute;
	background-color: var(--primary-theme-clr);
	bottom: -6px;
	left: 50%;
	border-radius: 10px;
	transform: translate(-50%);
	animation: widthUnderline 0.5s forwards;
}

nav.active {
	background-color: #8a53ff;
	color: #fff;
}

nav.active .nav__logo a {
	color: #fff;
}

nav.active .nav__menu .menu-item {
	color: #fff;
}

nav.active .mobile__menu .hamburger {
	--hamburger-menu-clr: #fff;
}

nav.active .nav__menu .signup-btn {
	background-color: #fff;
	border: 2px solid var(--primary-theme-clr);
	color: var(--primary-theme-clr);
}

@keyframes widthUnderline {
	0% {
		width: 0%;
	}

	100% {
		width: 50%;
	}
}

nav .nav__menu ul li .signup-btn {
	border: 0;
	text-decoration: none;
	background: var(--primary-theme-clr);
	color: var(--secondary-theme-clr);
	border: 2px solid var(--primary-theme-clr);
	border-radius: 999px;
	padding: 10px 30px;
	font-weight: 600;
}

nav .nav__menu ul li .signup-btn:hover {
	background: var(--secondary-theme-clr);
	color: var(--primary-theme-clr);
}

@media (max-width: 480px) {
	nav {
		padding: 0 11% 0 5%;
	}

    nav .nav__menu {
        display: none;
    }

    nav .mobile__menu {
        display: block;
    }

	nav .mobile__menu .hamburger {
		display: block;
	}

    nav .mobile__menu #hamburger:checked ~ .hamburger {
        transform: translateX(-150px);
        background-color: transparent;
        z-index: -1;
    }

    nav .mobile__menu #hamburger:checked ~ .hamburger::before {
        transform: rotate(30deg) translate(130px, -75px);
    }

    nav .mobile__menu #hamburger:checked ~ .hamburger::after {
        transform: rotate(330deg) translate(130px, 75px);
    }

    nav .mobile__menu #hamburger:checked ~ .menu-slider {
        height: 100vh;
    }
}
