footer {
	display: flex;
	background-color: #531ec4;
	width: 100%;
	height: 100%;
	padding: 40px;
	justify-content: space-around;
}

footer > div {
	color: var(--secondary-theme-clr);
}

footer > div h3 {
	font-size: 24px;
	text-decoration: underline;
}

footer > div ol li {
	list-style: none;
	padding: 10px 0px 10px;
	cursor: pointer;
}

footer > div ol li a {
	text-decoration: none;
	color: var(--secondary-theme-clr);
}

@media (max-width: 480px) {
	footer {
		flex-direction: column;
		padding: 20px;
	}

	footer > div {
		margin-bottom: 45px;
	}

	footer > div > h3 {
		font-size: 30px;
	}
}
