#pricing {
	background-image: url(../../../public/assets/images/pricing_top-left.svg);
	background-position: top left;
	background-repeat: no-repeat;
	height: 100%;
}

#pricing h1 {
	color: #8a53ff;
    font-size: 54px;
    text-align: center;
    padding: 70px 15px;
}

#pricing .container {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 70%;
	height: 600px;
    margin: 0px auto 150px;
}

#pricing .card {
	background-color: #fff;
	padding: 30px;
	width: 350px;
	height: 540px;
	margin: 10px 0px 10px;
	box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px,
		rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px,
		rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;
	position: relative;
}

#pricing .card:first-child {
	border-radius: 10px 0px 0px 10px;
}

#pricing .card:last-child {
	border-radius: 0px 10px 10px 0px;
}

#pricing h2.title {
	font-weight: 500;
	font-size: 36px;
}

#pricing h6.subtitle {
	text-align: left;
	color: #a9a9aa;
	font-weight: 300;
	font-size: 16px;
}

#pricing .amount > span {
	font-size: 12px;
	font-weight: 400;
}

#pricing .benefits {
	text-align: left;
}

#pricing .benefits > p {
	margin-top: 20px;
	font-size: 14.25px;
}

#pricing .status {
	position: absolute;
	bottom: 0;
	padding: 20px;
	left: 50%;
	transform: translate(-50%);
	width: 100%;
}

#pricing .status > .amount {
	font-weight: 500;
	font-size: 22px;
	text-align: left;
	margin-bottom: 10px;
}

#pricing .card > .status > .buy-now {
	color: #ff1d89;
	background-color: #fff5fa;
	border: none;
	width: 100%;
	padding: 15px;
	font-weight: 700;
	font-size: 16px;
	cursor: pointer;
	border-radius: 5px;
}

#pricing .box {
	width: 350px;
	height: 400px;
	box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px,
		rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px,
		rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;
	position: relative;
}

#pricing .card-special {
	background: #531ec4;
	border-radius: 20px;
	width: 350px;
	height: 650px;
	transform: translateY(-115px);
	padding: 30px;
}

#pricing .card-special > h2 {
	font-weight: 500;
	font-size: 30px;
	color: #fff;
}

#pricing .card-special > .benefits {
	color: #fff;
}

#pricing .card-special > .status > .amount {
	color: #fff;
	font-size: 32px;
}

#pricing .card-special > .status > .amount > span {
	font-size: 12px;
	font-weight: 400;
}

#pricing .strike {
	text-decoration: line-through;
}

#pricing .card-special > .status > button {
	color: #fff;
	background-color: #ff1d89;
	border: none;
	width: 100%;
	padding: 15px;
	font-weight: 600;
	font-size: 16px;
	cursor: pointer;
	border-radius: 5px;
	box-shadow: 0px 12px 32px rgba(255, 29, 137, 0.5);
}

@media (max-width: 480px) {
	#pricing {
		height: 100%;
		background-size: 60%;
		margin-bottom: 50px;
	}

	#pricing h1 {
		font-size: 34px;
		padding: 100px 15px 0;
	}

	#pricing .container {
		flex-direction: column;
		height: 100%;
		margin-top: 25px;
	}

	#pricing .card {
		margin-bottom: 145px;
		padding-bottom: 150px;
	}

	#pricing .card:first-child,
	#pricing .card:last-child {
		border-radius: 10px 10px 10px 10px;
	}

	#pricing .box {
		margin-bottom: 155px;
	}
}
